@font-face {
  font-family: "Gotham Pro";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/GothamPro.woff2") format("woff2"), url("../fonts/GothamPro.woff") format("woff");
}

@font-face {
  font-family: "Gotham Pro";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/GothamPro-Light.woff2") format("woff2"), url("../fonts/GothamPro-Light.woff") format("woff");
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  min-width: 320px;
  margin: 0 auto;
  font-family: "Gotham Pro", "Arial", "Helvetica", "Helvetica CY", "Nimbus Sans";
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 16px;
}

body.popup {
  max-height: 100vh;
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: -1px;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hidden {
  display: none;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input::-ms-clear {
  display: none;
}

input:-moz-focusring {
  outline: none;
  border-color: #00adef;
  box-shadow: none;
}

input:invalid {
  outline: none;
  box-shadow: none;
}

.button {
  display: block;
  height: 48px;
  padding: 15px 40px;
  font-size: 15px;
  line-height: 17px;
  text-align: center;
  color: #ffffff;
  background-color: #00adef;
  border: none;
  border-radius: 24px;
  transition: all 0.2s ease;
}

.button:hover {
  cursor: pointer;
  background-color: #4687e0;
}

.button:focus {
  outline: none;
  background-color: #4687e0;
}

.button:active {
  background-color: #0054a6;
  color: #888;
  fill: #888;
}

.button--white {
  color: #000000;
  background-color: #ffffff;
  border: 1px solid #e7e7e7;
}

.button--white:hover {
  color: #00adef;
  border-color: #00adef;
  background-color: #ffffff;
}

.button--white:focus {
  color: #00adef;
  border-color: #00adef;
  background-color: #ffffff;
}

.button--white:active {
  color: #0054a6;
  border-color: #0054a6;
  fill: #0054a6;
}

.button:disabled {
  background-color: #dddddd;
}

.promo {
  background: linear-gradient(180deg, #7FB0F8, #D5D3FF);
}

.promo__container {
  margin: 0 auto;
  max-width: 1516px;
  padding-left: 53px;
  padding-right: 53px;
  position: relative;
  padding-top: 296px;
  min-height: 999px;
  background-image: url("../img/app-showcase-icon.png");
  background-repeat: no-repeat;
  background-position: top 103px right 53px;
}

@media (max-width: 1399px) {
  .promo__container {
    padding-left: 5.18%;
    padding-right: 5.18%;
  }
}

@media (max-width: 1023px) {
  .promo__container {
    padding-left: 4.17%;
    padding-right: 4.17%;
  }
}

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .promo__container {
    background-image: url("../img/app-showcase-icon@2x.png");
    background-size: 690px 813px;
  }
}

.promo__title {
  margin: 0;
  margin-bottom: 50px;
  max-width: 46%;
  font-weight: 300;
  font-size: 56px;
  line-height: 61px;
  letter-spacing: 0.56px;
}

.promo__description {
  margin: 0;
  margin-bottom: 50px;
  max-width: 43%;
  font-weight: 300;
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 0.63px;
}

.promo__google-pay-link,
.promo__app-store-link {
  transition: all 0.2s ease;
  display: inline-block;
  width: 162px;
  height: 48px;
  background-repeat: no-repeat;
}

.promo__google-pay-link:hover,
.promo__app-store-link:hover {
  opacity: 0.8;
}

.promo__google-pay-link:active,
.promo__app-store-link:active {
  opacity: 0.5;
}

.promo__google-pay-link {
  margin-right: 12px;
  background-image: url("../img/icon_googlepay_white.svg");
}

.promo__app-store-link {
  background-image: url("../img/icon_appstore_white.svg");
}

.promo__scroll-down {
  position: absolute;
  bottom: 25px;
  left: 50%;
  display: block;
  width: 25px;
  height: 42px;
  margin-left: -12px;
  background-image: url("../img/icon_down_white.svg");
  background-repeat: no-repeat;
  background-position: top 14px left 0;
}

.promo__scroll-down::before, .promo__scroll-down::after {
  transition: all 0.5s ease;
  content: "";
  position: absolute;
  left: 0;
  width: 25px;
  height: 14px;
  background-image: url("../img/icon_down_white.svg");
  background-repeat: no-repeat;
  opacity: 0;
}

.promo__scroll-down::before {
  top: 0;
}

.promo__scroll-down::after {
  bottom: 0;
}

.promo__scroll-down:hover {
  cursor: pointer;
}

.promo__scroll-down:hover::before, .promo__scroll-down:hover::after {
  opacity: 1;
}

